const FAQItem = ({ question, answer }) => {
  const [isOpen, setIsOpen] = useState(false);
  return (
    <div className="faq-item border-b border-green-200 py-4">
      <button
        className="faq-question flex justify-between items-center w-full text-left"
        onClick={() => setIsOpen(!isOpen)}
        aria-expanded={isOpen}
        aria-controls={`faq-${question.replace(/\s+/g, '-')}`}
      >
        <span className="font-semibold">{question}</span>
        {isOpen ? <ChevronUp size={20} className="text-green-600" /> : <ChevronDown size={20} className="text-green-600" />}
      </button>
      {isOpen && (
        <p id={`faq-${question.replace(/\s+/g, '-')}`} className="faq-answer mt-2 text-gray-600 transition duration-300">
          {answer}
        </p>
      )}
    </div>
  );
};

